.contact{
    padding: 2rem;
    
}

.c-container{
    display: flex;
    gap:2rem;
    background-image: var(--linearcolor);
    overflow: hidden;
    width: 100%;
    height: 45%;
    margin-top: -2rem;
}

.c-content{
    padding: 2rem;
    display: table-row;
    margin-left: 2rem;
    
}

.c-container img{
  padding: 1rem;
    height: 40rem;
    width: 30rem; 
    margin-left: 7rem;
    position: relative;
  }

.contact .c-content form{
    flex:1 1 50rem;
    padding:1rem;
  }
  
  .contact .c-content form .inputBox{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
.contact .c-content form .inputBox input, 
.order .row form textarea{
    padding:1rem;
    margin:1rem 0;
    font-size: 1.2rem;
    color:#333;
    text-transform: none;
    border:.1rem solid rgba(0,0,0,.3);
    border-radius: .5rem;
    width:49%;
  }
  
  .contact .c-content form textarea{
    width:100%;
    resize: none;
    height:9rem;
  }
  
  .contact .c-content form .btn{
    background:none;
  }
  
  .contact .c-content form .btn:hover{
    background:var(--red);
  }

  @media screen and (max-width: 480px) {
    .c-container {
      flex-direction: column;
      gap: 2rem;
      height: 80rem;
    }

    .c-container img{
        margin-left: 4rem;
       
      }


  }